import { DEFAULT_BULLET_LIST_ARRAY, DEFAULT_NUMBER_LIST_ARRAY } from '../utils';

export const LIST_TOGGLE_LIST = Symbol('LIST_TOGGLE_LIST');

export const LIST_TRANSFORM = Symbol('LIST_TRANSFORM');

export const LIST_REMOVE_LIST = Symbol('LIST_REMOVE_LIST');

export const LIST_SET_CHECK_STATUS = Symbol('LIST_SET_CHECK_STATUS');

export const LIST_PUT_LIST_ORDER = Symbol('LIST_PUT_LIST_ORDER');

export const LIST_SELECT_SYMBOL = Symbol('LIST_SELECT_SYMBOL');

export const CLEAR_RANGE_SELECTION = Symbol('CLEAR_RANGE_SELECTION');

export const SELECT_LIST_SELECTIONS = Symbol('SELECT_LIST_SELECTIONS');

export const CLEAR_LIST_SELECTIONS = Symbol('CLEAR_LIST_SELECTIONS');

export const RESTART_NUMBERING = Symbol('ACTION_RESTART_NUMBERING');

export const CONTINUE_NUMBERING = Symbol('ACTION_CONTINUE_NUMBERING');

export function toggleList(listType, styleType?) {
  // TODO 全量后默认值移至mobileNativeToolbar/utils中
  let listStyleType;
  if (!styleType) {
    if (listType === 'olist') {
      listStyleType = DEFAULT_NUMBER_LIST_ARRAY[0];
    } else if (listType === 'ulist') {
      listStyleType = DEFAULT_BULLET_LIST_ARRAY[0];
    }
  } else {
    listStyleType = styleType;
  }
  return {
    type: LIST_TOGGLE_LIST,
    payload: { listType, listStyleType },
  };
}

export function transformList(listType) {
  let listStyleType;
  if (listType === 'olist') {
    listStyleType = DEFAULT_NUMBER_LIST_ARRAY[0];
  } else if (listType === 'ulist') {
    listStyleType = DEFAULT_BULLET_LIST_ARRAY[0];
  }
  return {
    type: LIST_TRANSFORM,
    payload: { listType, listStyleType },
  };
}

export function removeList() {
  return {
    type: LIST_REMOVE_LIST,
  };
}

export function setCheckStatus(node, checked: boolean) {
  return {
    type: LIST_SET_CHECK_STATUS,
    payload: {
      node,
      checked,
    },
  };
}

export function selectSymbol(listSelections, isSelected) {
  return {
    type: LIST_SELECT_SYMBOL,
    payload: {
      listSelections,
      isSelected,
    },
  };
}

export function clearRangeSelection(node) {
  return {
    type: CLEAR_RANGE_SELECTION,
    payload: { node },
  };
}

export function selectListSelections(listSelections) {
  return {
    type: SELECT_LIST_SELECTIONS,
    payload: { listSelections },
  };
}

export function clearListSelections() {
  return {
    type: CLEAR_LIST_SELECTIONS,
  };
}

export function restartNumbering() {
  return {
    type: RESTART_NUMBERING,
  };
}

export function continueNumbering() {
  return {
    type: CONTINUE_NUMBERING,
  };
}


/** @deprecated */
export const DEPRECATED_ACTIONS = {
  CONTINUE_NUMBERING,
  CLEAR_LIST_SELECTIONS,
  CLEAR_RANGE_SELECTION,
  LIST_REMOVE_LIST,
  LIST_SET_CHECK_STATUS,
  LIST_SELECT_SYMBOL,
  LIST_TOGGLE_LIST,
  LIST_TRANSFORM,
  RESTART_NUMBERING,
  SELECT_LIST_SELECTIONS,
  LIST_PUT_LIST_ORDER,

  selectSymbol,
  clearListSelections,
  toggleList,
  transformList,
  removeList,
  setCheckStatus,
  clearRangeSelection,
  selectListSelections,
  restartNumbering,
  continueNumbering,
};

export const actions = {
  selectSymbol,
  clearListSelections,
  toggleList,
  transformList,
  removeList,
  setCheckStatus,
  clearRangeSelection,
  selectListSelections,
  restartNumbering,
  continueNumbering,
};
